<template>
  <div v-if="visibleMessages.length > 0" class="important-messages">
    <div 
      v-for="(message, index) in visibleMessages" 
      :key="index" 
      class="message" 
      :class="getMessageTypeClass(message.type)"
    >
      <div 
        class="message-content" 
        @click="handleMessageClick"
      >
        <div class="message-content-inner" v-html="message.message"></div>
      </div>
      <button 
        v-if="message.is_hideable === 1" 
        class="hide-button" 
        @click="hideMessage(message.identifier)"
      >
        ×
      </button>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import Vue from 'vue';

export default {
  name: 'ImportantMessages',
  data() {
    return {
      hiddenMessageIds: []
    };
  },
  computed: {
    ...mapState({
      config: state => state.config,
      currentDevice: state => state.currentDevice
    }),
    importantMessages() {
      return this.$store.getters.importantMessages || [];
    },
    visibleMessages() {
      // Filter out hidden messages
      return this.importantMessages.filter(message => {
        return message.is_hideable !== 1 || !this.hiddenMessageIds.includes(message.identifier);
      });
    }
  },
  methods: {
    handleMessageClick(event) {
      // Check if the clicked element is a link
      let target = event.target;
      
      // If it's not the link itself, check if it's within a link
      if (target.tagName !== 'A') {
        target = target.closest('a');
      }
      
      // If we didn't find a link, do nothing
      if (!target) {
        return;
      }
      
      // If it's a link with data-action attribute, handle it with the global action handler
      if (target.hasAttribute('data-action')) {
        event.preventDefault();
        const action = target.getAttribute('data-action');
        
        // Just simulate a direct click on the document to trigger the global handler
        const actionEl = document.createElement('button');
        actionEl.setAttribute('data-action', action);
        actionEl.style.display = 'none';
        document.body.appendChild(actionEl);
        actionEl.click();
        document.body.removeChild(actionEl);
        return;
      }
      
      // Check if it's an internal link by examining the href attribute
      const href = target.getAttribute('href');
      
      // If it's an internal link (starts with '/' and doesn't have 'http://', 'https://', etc.)
      if (href && href.startsWith('/') && !href.match(/^(https?:|mailto:|tel:|ftp:|#)/)) {
        event.preventDefault();
        
        // Use Vue Router for navigation instead of browser default behavior
        this.$router.push(href);
      }
    },
    getMessageTypeClass(type) {
      switch (type) {
        case 'info':
          return 'message-info';
        case 'notice':
          return 'message-notice';
        case 'warning':
          return 'message-warning';
        default:
          return 'message-info';
      }
    },
    hideMessage(identifier) {
      // Add message id to hidden list
      if (!this.hiddenMessageIds.includes(identifier)) {
        this.hiddenMessageIds.push(identifier);
        this.saveHiddenMessages();
      }
    },
    loadHiddenMessages() {
      try {
        const savedIds = localStorage.getItem('hiddenMessageIds');
        if (savedIds) {
          this.hiddenMessageIds = JSON.parse(savedIds);
        }
      } catch (e) {
        console.error('Error loading hidden message IDs:', e);
        this.hiddenMessageIds = [];
      }
    },
    saveHiddenMessages() {
      try {
        // Clean up any IDs that are no longer in the important_messages
        this.cleanupHiddenMessages();
        localStorage.setItem('hiddenMessageIds', JSON.stringify(this.hiddenMessageIds));
      } catch (e) {
        console.error('Error saving hidden message IDs:', e);
      }
    },
    cleanupHiddenMessages() {
      // Remove IDs that no longer exist in the important_messages array
      if (this.importantMessages && this.importantMessages.length > 0) {
        const validIdentifiers = this.importantMessages
          .filter(msg => msg.is_hideable === 1)
          .map(msg => msg.identifier);
        
        this.hiddenMessageIds = this.hiddenMessageIds.filter(id => 
          validIdentifiers.includes(id)
        );
      }
    }
  },
  created() {
    this.loadHiddenMessages();
  },
  watch: {
    importantMessages: {
      handler() {
        this.cleanupHiddenMessages();
      },
      deep: true
    }
  }
};
</script>

